import { useEffect, useState } from "react"
import storageHelper from "./storageHelper";

export const useAsync = <T>(cb: { (): Promise<T> }, args: any[] = [], cacheKey?: string) => {
  const [val, setVal] = useState<T | null>(cacheKey ? storageHelper.get(cacheKey) : null);
  useEffect(() => {
    cb().then(res => {
      setVal(res);
      if (cacheKey) storageHelper.set(cacheKey, res);
    });
  }, args);
  return val;
};
